import Vue from 'vue'
// state
const state = {
  breadcrumbBgColor:"#E5EAED",
  scrollTop:0,
}



// getters
const getters = {
  breadcrumbBgColor(state){
    return state.breadcrumbBgColor
  },
  scrollTop(state){
    return state.scrollTop
  }, 
}

// mutations
const mutations = {
  setBreadcrumbBgColor(state, color){
    state.breadcrumbBgColor = color
  },
  setScrollTop(state, scrollTop){
    state.scrollTop = scrollTop
  },
}

// actions
const actions = {

}

export default {
  state,
  getters,
  actions,
  mutations
}