import Vue from 'vue'
import store from './store'
import Router from 'vue-router'
import Layout from './views/layout/layout'

const Login =     () => import("@/views/login")
const VoiceReg =     () => import('@/views/voiceReg')
const Reccognition =     () => import('@/views/reccognition')
const MultipleReccognition =     () => import('@/views/multipleReccognition')

Vue.use(Router)

export const unLoginRouters = [

]

export const needLoginRouters = [
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title: "登录页",
    }
  },
  {
    path: '/',
    component: Layout,
    redirect: '/voiceReg',
    children: [{
      path: 'voiceReg',
      name: 'VoiceReg',
      component: VoiceReg,
      meta: {
        title: "声纹注册",
      }
    }]
  },
  {
    path: '/',
    component: Layout,
    children: [{
      path: 'reccognition',
      name: 'Reccognition',
      component: Reccognition,
      meta: {
        title: "声纹识别",
      }
    }]
  },
  {
    path: '/',
    component: Layout,
    children: [{
      path: 'multipleReccognition',
      name: 'MultipleReccognition',
      component: MultipleReccognition,
      meta: {
        title: "对话识别",
      }
    }]
  },
]

export const Routes = unLoginRouters.concat(needLoginRouters)

export default new Router({
  //mode: "history",
  routes:Routes,
  scrollBehavior (to, from, savedPosition) {
    // if (savedPosition) {
    //   store.commit('setScrollTop',savedPosition.y)
    //   return savedPosition
    // } else {
    //   return { x: 0, y: 0 }
    // }
    return { x: 0, y: 0 }
  },
})
