import Vue from 'vue'
import Cookies from 'js-cookie'
import { login, logout } from "@/api/login";

// state
const state = {
  userInfo: {
    id: null,
    username: null,
    email: null,
    type: null,
  }
}



// getters
const getters = {
  isLogin(state) {
    let sspUser_SESSION = Cookies.get("sspUser")
    let isLogin = (sspUser_SESSION != null && sspUser_SESSION != "")
    return isLogin
  },
  userInfo(state) {
    return state.userInfo
  },
  isAdmin(state) {
    return state.userInfo.type === "管理员"
  }
}

// mutations
const mutations = {
  setUserInfo(state, userInfo) {
    state.userInfo = userInfo
  },
  delCookie(state) {
    Cookies.remove("sspUser")
  },
  delUserInfo(state) {
    state.userInfo = {
      id: null,
      username: null,
      email: null,
      type: null,
    }
  },
  logout() {
    this.commit("delCookie")
    this.commit("delUserInfo")
  }
}

// actions
const actions = {
  login({ commit }, { username, password }) {
    return new Promise((resolve, reject) => {
      login({ username, password })
        .then(res => {
          if (res.success) {
            commit('setUserInfo', res.data)
            resolve()
          }
          else {
            reject()
          }
        })
        .catch(e => {
          reject()
        })
    })
  },
  logout({ commit }) {
    return new Promise((resolve, reject) => {
      logout()
        .then(res => {
          if (res.success) {
            commit('setUserInfo', {
              id: null,
              account: null,
              name: null,
              userType: null,
            })
            resolve()
          }
          else {
            reject()
          }
        })
        .catch(e => {
          reject()
        })
    })
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}